import React from "react";
import "./App.css";
import { useContext,useState } from "react";
import CreateBlog from "./Components/CreateBlog/Form";
import BlogProvider from "./Store/Blog-provider";
import { Route, Switch,Redirect } from "react-router-dom";
import ExpandedBlog from "./Components/ViewBlog/Lists/Blog";
import Header from "./Components/Header/Header";
import BlogList from "./Components/ViewBlog/Lists/BlogList";
import AuthForm from "./Components/auth/AuthForm";
import AuthContext from "./Store/auth-context";
function App() {
 // useState()
 const [value,setValue]=useState(false);
  const loadingFnc=()=>{
    console.log('done');
    setValue(true);
  }
  const normalFnc=()=>{
   setValue(false);
  }
  const AuthCtx=useContext(AuthContext);
  return (
    <Switch>
      <BlogProvider>
        <Route path='/' exact>
        <Redirect to='/home'/>
        </Route>
        <Header/> 

    <Route path="/Home" exact>
     
     <BlogList loading={loadingFnc} endLoading={normalFnc}/>
     </Route>
      <Route path="/Blogs/:blogId">
       <ExpandedBlog isLoading={loadingFnc} endLoading={normalFnc}/>
      </Route>
    

      <Route path="/Create" exact>
      {AuthCtx.isLoggedIn&&<CreateBlog/>}
      {!AuthCtx.isLoggedIn && <Redirect to='/home'/>}
      
      </Route>
      <Route path="/Auth" exact>
       <AuthForm/>
      </Route>
      {!value&&(<footer className="footer">
        <div className='icon'>
        <a href="https://github.com/iamgope"><span class="fa fa-github" ></span></a> 
     <a href="https://www.facebook.com/"><span class="fa fa-facebook" ></span></a> 
     <a href="https://twitter.com/Iamgope"><span class="fa fa-twitter" ></span></a> 
     <a href="https://www.linkedin.com/in/iamgope/"><span class="fa fa-linkedin" ></span></a> 


        </div>
    
      </footer>)}
     </BlogProvider>
     
    </Switch>
  );
}

export default App;
